.Menu{
  display: flex;
  flex-direction: row;
  height: calc(20px + 2vmin);
  position: fixed;
  top: 0;
}
.Menu img{
  height: 100%;
}
.Menu-link{
  width: 100%;
  text-align: justify;
  border-radius: 40%;
  background-color: black;
  font-size: 97%;
  color: green;
}
.Menu-link span{
  padding-right: 20%;
  padding-left: 20%;
  cursor: alias;
}
.Menu-link:hover{
  background-color:#0fc552;
  color:red;
}
